// jwt는 보통 localstorage에 저장하는데 이러면 csrf공격에 취약해진다고 함
// 해서 보통은 jwt와 csrf방지 기능을 함께 쓴다고 함

import axios from "axios";
import React from "react";

axios.defaults.xsrfCookieName = "csrftoken";
axios.defaults.xsrfHeaderName = "X-CSRFToken";

function getCookie(name) {
  var cookieValue = null;
  if (document.cookie && document.cookie !== "") {
    var cookies = document.cookie.split(";");
    for (var i = 0; i < cookies.length; i++) {
      var cookie = cookies[i].replace(" ", "");
      //var cookie = jQuery.trim(cookies[i]);
      // 당신이 만약 jQuery를 사용한다면, 위 코드 대신 이 코드를 사용하여도 좋다
      if (cookie.substring(0, name.length + 1) === name + "=") {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}
const CSRFToken = () => {
  const csrftoken = getCookie("csrftoken");
  return <input type="hidden" name="csrfmiddlewaretoken" value={csrftoken} />;
};

export default CSRFToken; // 이걸로 <CSRFToken/>이라는 컴포넌트를 사용할 수 있게 됨.

